body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

#page-wrap {
  padding-bottom: 10px;
  padding-top: 10px;
}
.App {
  text-align: center;
   
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
}

header {
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: xx-large;
  
  
}

body {
  background-color: darkred;
}

/*Clothes*/
.list {
  display: flex;
  flex-direction: column;
}

.list .item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item span {
color: rgb(27, 152, 255);
}




/*Contact*/
/* Style inputs */

* {
  box-sizing: border-box;
}

p {
  color: white;
}


.container {
  overflow: hidden;
}


input[type=text]{

  padding:12px 12px;
  
  display: block;
  border: 2px solid #ccc;
  border-radius:4px;
  

}

.container label {
  text-align: left;
  
}


input[type=submit]{
  background-color: rgb(103, 169, 212);
  margin: 8px auto;
  padding: 10px 10px;
  border: 3px black;
  padding: 15px;
  font-family: sans-serif;
  border-radius:5px;
  cursor: pointer;
  text-align: center;
  width: 100px;
}


input[type=submit]:hover{
  background-color: rgb(23, 143, 190);
}

.container{
  border-radius: 5px;
  background-color:#f2f2f2;
  padding: 20px;
}

fieldset {
  display: flex;
  flex-direction: column;

}

fieldset > p {
  text-align: left;
  font-size: 13px;
  color: red;
}


/* Media query for mobile devices */
@media screen and (max-width: 767px) {
  header {
    font-size: large;
  }
  

  fieldset > p {
    font-size: 11px;
    display: inline-block;
    
    
  }
  
  .list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

  .list .item {
  margin: 10px;
  width: 45%;
}



}
/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #a90000;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }

/* Individual item */
.bm-item {
    display: inline-block;
  
    color: #d1d1d1;
    margin-bottom: 10px;
    text-align: left;
    text-decoration: none;
    transition: color 0.2s;
  }
  
  .bm-item:hover {
    color: #ffffff;
  }
